xt-white font-semibold px-4 py-2 rounded transition ease-in-out duration-150 items-center cursor-not-allowed')\n</script>\n\n<style scoped>\n.fade-enter-active, .fade-leave-active {\n  transition: opacity 1s ease;\n}\n.fade-enter, .fade-leave-to {\n  opacity: 0;\n}\n</style>\n"],"sourceRoot":""}]);
